@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0;
  }
}

.header {
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  z-index: 100;
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding-top: 30px;
  }
}

@media (max-width: 767px) {
  .header__wrapper--closed {
    padding: 0 15px;
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .header__wrapper--opened {
    background-color: #f9fbfd;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: start;
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: 36px;
    min-height: 100vh;
  }
}

@media (max-width: 767px) {
  .header__wrapper a {
    width: 42px;
  }
}
@media (hover: hover) {
  .header__wrapper a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__wrapper a.focus-visible:focus {
  opacity: 0.8;
}

.header__logo {
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .header__wrapper--opened svg {
    fill: #011c40;
  }
}

.nav {
  position: relative;
  padding-top: 8px;
}
@media (max-width: 1023px) {
  .nav {
    padding-top: 0;
  }
}

.nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .nav__toggle {
    position: relative;
    display: block;
    width: 24px;
    height: 12px;
    top: 4px;
    background-color: transparent;
    border: none;
  }
}

.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
}

@media (max-width: 767px) {
  .nav--opened .nav__toggle {
    position: absolute;
    top: -18px;
    right: -9px;
  }
}

.nav--opened .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 2px;
  background-color: #011c40;
  transform: rotate(45deg);
}

.nav--opened .nav__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 2px;
  background-color: #011c40;
  transform: rotate(-45deg);
}

.nav ul {
  display: flex;
  flex-direction: row;
  column-gap: 34px;
  align-items: center;
}
@media (max-width: 1023px) {
  .nav ul {
    column-gap: 24px;
  }
}
@media (max-width: 767px) {
  .nav ul {
    flex-direction: column;
    column-gap: 0;
    align-items: start;
    padding-top: 25px;
    row-gap: 9px;
  }
}

@media (max-width: 767px) {
  .nav li {
    width: 100%;
  }
}

.nav a {
  padding: 15px 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .nav a {
    padding: 10px 0;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav a {
    display: block;
    padding: 10px;
    color: #011c40;
    font-size: 18px;
    line-height: 22px;
    width: 100%;
  }
}
@media (hover: hover) {
  .nav a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.nav a.focus-visible:focus {
  opacity: 0.8;
}

@media (max-width: 767px) {
  .nav--closed ul {
    display: none;
  }
}

